.novel-vertical-menu {
  position: relative;
  display: flex;
  align-items: flex-start;
  height: 100%;
  width: 100%;
  z-index: 1000;
  margin: 0 !important;
  padding: 0 !important;
  flex-direction: row-reverse; // 아이콘을 우측에 배치

  .icon-menu {
    display: flex;
    flex-direction: column;
    gap: 8px;
    padding: 16px 8px;
    background: var(--bg-primary);
    border-radius: 0; // 우측 끝이므로 모서리 둥글게 하지 않음
    border-left: 1px solid var(--border-color);
    border-top: 1px solid var(--border-color);
    border-bottom: 1px solid var(--border-color);
    border-right: none; // 우측 테두리 제거 (화면 끝에 붙음)
    box-shadow: -2px 0 8px var(--shadow);
    height: 100%;
    width: 60px;

    .menu-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 44px;
      height: 44px;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      position: relative;
      background: transparent;

      .icon {
        font-size: 20px;
        transition: transform 0.2s ease;
      }

      &:hover {
        background: var(--bg-accent);
        transform: translateX(-2px); // 우측 메뉴는 좌측으로 이동
        box-shadow: 0 2px 8px var(--shadow);

        .icon {
          transform: scale(1.1);
        }
      }

      &.active {
        background: var(--accent-color);
        color: white;
        box-shadow: 0 4px 12px var(--shadow);

        .icon {
          transform: scale(1.05);
        }
      }

      // 툴팁 효과 (우측 메뉴용)
      &::after {
        content: attr(title);
        position: absolute;
        right: 100%; // 좌측에 표시
        top: 50%;
        transform: translateY(-50%);
        margin-right: 8px; // 우측 마진
        padding: 4px 8px;
        background: rgba(0, 0, 0, 0.8);
        color: white;
        border-radius: 4px;
        font-size: 12px;
        white-space: nowrap;
        opacity: 0;
        pointer-events: none;
        transition: opacity 0.2s ease;
        z-index: 1001;
      }

      &:hover::after {
        opacity: 1;
      }

      &.active::after {
        display: none;
      }
    }
  }

  .expanded-panel {
    position: absolute;
    right: 60px; // 아이콘 메뉴의 좌측에 배치
    top: 0;
    height: 100%;
    width: 190px; // 전체 250px에서 아이콘 메뉴 60px를 뺀 크기
    background: var(--bg-primary);
    border-radius: 0; // 모서리 둥글게 하지 않음
    box-shadow: -2px 0 12px var(--shadow);
    overflow: hidden;
    border-top: 1px solid var(--border-color);
    border-left: 1px solid var(--border-color);
    border-bottom: 1px solid var(--border-color);
    border-right: none;

    .panel-content {
      padding: 20px;
      height: 100%;
      overflow-y: auto;

      .panel-header {
        border-bottom: 1px solid var(--border-color);
        padding-bottom: 16px;
        margin-bottom: 20px;

        .panel-title {
          display: flex;
          align-items: center;
          gap: 12px;
          margin-bottom: 8px;

          .panel-icon {
            font-size: 24px;
            padding: 8px;
            background: var(--bg-tertiary);
            border-radius: 8px;
          }

          h3 {
            margin: 0;
            font-size: 18px;
            font-weight: 600;
            color: var(--text-primary);
          }
        }

        .panel-description {
          margin: 0;
          color: var(--text-secondary);
          font-size: 14px;
          margin-left: 56px;
        }
      }

      .panel-actions {
        display: flex;
        flex-direction: column;
        gap: 8px;

        .action-item {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 12px 16px;
          border-radius: 8px;
          cursor: pointer;
          transition: all 0.2s ease;
          border: 1px solid transparent;

          .action-content {
            flex: 1;

            .action-label {
              font-weight: 500;
              color: #1f2937;
              font-size: 14px;
              margin-bottom: 4px;
            }

            .action-description {
              font-size: 12px;
              color: #6b7280;
              line-height: 1.3;
            }
          }

          .action-arrow {
            color: #9ca3af;
            font-size: 14px;
            transition: all 0.2s ease;
          }

          &:hover:not(.disabled) {
            background: #f8fafc;
            border-color: #e5e7eb;
            transform: translateX(-2px); // 우측 메뉴는 좌측으로 이동

            .action-arrow {
              color: #3b82f6;
              transform: translateX(-2px); // 화살표도 좌측으로
            }
          }

          &.disabled {
            opacity: 0.5;
            cursor: not-allowed;
            background: #f9fafb;

            .action-label {
              color: #9ca3af;
            }

            .action-description {
              color: #d1d5db;
            }
          }
        }
      }

      .generation-status {
        margin-top: 20px;
        padding: 16px;
        background: #f8fafc;
        border-radius: 8px;
        border: 1px solid #e5e7eb;

        .status-item {
          display: flex;
          align-items: center;
          gap: 8px;
          margin-bottom: 8px;

          .status-icon {
            font-size: 16px;
          }

          .status-text {
            font-size: 14px;
            font-weight: 500;
          }

          &.generating {
            .status-icon {
              animation: pulse 2s infinite;
            }
            .status-text {
              color: #f59e0b;
            }
          }

          &.completed {
            .status-text {
              color: #10b981;
            }
          }
        }

        .current-step {
          display: flex;
          gap: 8px;
          font-size: 13px;

          .step-label {
            color: #6b7280;
          }

          .step-value {
            color: #1f2937;
            font-weight: 500;
          }
        }
      }

      .export-info {
        margin-top: 20px;
        padding: 16px;
        background: #f0f9ff;
        border-radius: 8px;
        border: 1px solid #bae6fd;

        .info-item {
          display: flex;
          flex-direction: column;
          gap: 4px;

          .info-label {
            font-size: 12px;
            color: #0369a1;
            font-weight: 500;
          }

          .info-value {
            font-size: 13px;
            color: #1e40af;
            line-height: 1.4;
          }
        }
      }
    }
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

// 반응형 처리
@media (max-width: 768px) {
  .novel-vertical-menu {
    .icon-menu {
      width: 50px;
      padding: 12px 6px;

      .menu-icon {
        width: 38px;
        height: 38px;

        .icon {
          font-size: 18px;
        }
      }
    }

    .expanded-panel {
      left: 50px;
      width: 200px; // 모바일에서는 좀 더 넓게

      .panel-content {
        padding: 16px;
      }
    }
  }
}